.footer {
  display: flex;
  justify-content: center;
  align-items: flex-end;

  padding-bottom: 24px;
  height: 320px;

  background-image: url(../images/footer.svg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @include onDesktop {
    height: 360px;
    padding-bottom: 36px;
  }
}
